import axios from "axios";

function logErr({ url, error }) {
  if (process.env.NODE_ENV == "development")
    console.error("An error encounted while fetching from :" + url, error);
}

export default {
  api() {
    return axios.create({
      baseURL: `${window.origin}/api`,
      withCredentials: false, // This is the default
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      timeout: 10000
    });
  },
  get(url, obj) {
    return this.api()
      .get(url, obj)
      .then(response => {
        return response;
      })
      .catch(error => {
        logErr({ url, error });
        throw error;
      });
  },
  post(url, data) {
    return this.api()
      .post(url, data)
      .then(response => {
        return response;
      })
      .catch(error => {
        logErr({ url, error });
        throw error;
      });
  },
  put(url, data) {
    return this.api()
      .put(url, data)
      .then(response => {
        return response;
      })
      .catch(error => {
        logErr({ url, error });
        throw error;
      });
  },
  delete(url, config) {
    return this.api()
      .delete(url, config)
      .then(response => {
        return response;
      })
      .catch(error => {
        logErr({ url, error });
        throw error;
      });
  }
};
